<template>
  <div class="floor-building">
    <img class="building-foundation" :src="require('@/assets/building-foundation.png')" alt="">
    <div class="floors">
      <transition-group
        name="staggered-fade"
        tag="ul"
        :css="false"
        @before-enter="beforeEnter"
        @enter="enter"
        @leave="leave"
      >
        <FloorBuildingItem
          v-for="(item, index) in list"
          :key="item.id"
          :status="item.status"
          :data-index="index"
          :index="index"
          @click="handleSelectFloor(index)" />
      </transition-group>
    </div>
  </div>
</template>

<script>
import { FloorBuildingItem } from '@/components'

export default {
  props: {
    floors: {
      type: Number,
      default: 25
    },
    list: {
      type: Array,
      default: () => []
    },
    delay: {
      type: Number,
      default: 0.25
    },
    curFloor: {
      type: Number,
      default: 1
    }
  },
  components: {
    FloorBuildingItem
  },
  data() {
    return {
      
    }
  },
  mounted() {
    // let floors = this.floors
    // this.initFloorList(floors)
  },
  methods: {
    initFloorList(floors) {
      let arr = []

      for(let i = 0; i < floors; i++) {
        let obj = {
          status: i % this.limit + 1,
          id: i + 1
        }

        arr.push(obj)
      }

      this.list = arr
    },
    beforeEnter: function (el) {
      el.style.opacity = 0
    },
    enter: function (el, done) {
      var delay = el.dataset.index * this.delay

      setTimeout(function () {
        Velocity(
          el,
          { opacity: 1 },
          { complete: done }
        )
      }, delay)
    },
    leave: function (el, done) {
      var delay = el.dataset.index * this.delay 

      setTimeout(function () {
        Velocity(
          el,
          { opacity: 0 },
          { complete: done }
        )
      }, delay)
    },
    setCurFloorStatus(curFloor) {
      //  console.log('setCurFloorStatus curFloor', curFloor)
      let statusNum = Math.floor(Math.random() * 8)
      statusNum = statusNum === 0 ? 1 : statusNum

      //  console.log('setCurFloorStatus curFloor', curFloor)
      this.list[curFloor - 1].status = statusNum
    },
    handleSelectFloor(index) {
      console.log('handleSelectFloor', index)
    }
  },
  watch: {
    curFloor(val) {
      this.setCurFloorStatus(val)
    }
  }
}
</script>

