    position: absolute;
    top: 198px;
    left: 120px;

    img.building-foundation {
      width: 479px;
      height: 386px;
      display: block;
      position: absolute;
      left: 32px;
      bottom: -5px;
    }

    
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity 6.5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>